//----- MIXIN ----->>>>>
$text-shade-white: 1px 1px 1px #fff;


//----- NAVIGATION ----->>>>>
nav,
.navbar-inverse,
.navbar-fixed-top {
	//width: 100%
	height: 50px;
	@include base-box;
	color: #fff;
	box-sizing: border-box;
 }	//background-color: rgba(20, 20, 20, .8)
.container-fluid {
	@include no-cush;
	height: 50px;
 }	//background-color: rgba(20, 20, 20, .8)

.navbar-header {
	//display: flex
	//width: 30%
	height: 50px;
	color: #fff; }
.navbar-brand {
	padding-left: 20px;
	color: #fff;
	font-size: 2rem;
	text-shadow: $text-shade-green;
 }	//background-color: rgba(20, 20, 20, .8)

.myNavbar-wrap .navbar-right {
	//width: 50%
	margin-right: 20px;
	//@include base-box
 }	//background-color: rgba(255, 255, 255, .8)

#myNavbar {
	//padding-right: 20px
	//@include base-box
	color: #fff;
	ul {
		//display: flex
		width: 100%;
		margin: 0 10px 0 0;
		box-sizing: border-box;
		color: #121212;
		background-color: rgba(255, 255, 255, .6);
		li {
			flex: 1;
			color: #121212;
			@include base-box;
			border-bottom: thin solid #121212;
			background-color: $clr-green;
			font-weight: bolder;
			&:hover {
				color: #fff;
				border-bottom: thin solid #fff; }
			//background-color: rgba(255, 255, 255, .4)
			a {
				height: 50px;
				@include font-link;
				font-weight: 600;
				color: #222;
				//background-color: rgba(255, 255, 255, .4)
				&:hover {
					color: #fff;
 } }					//border-bottom: thin solid #fff
			@media (max-width: 550px) and (orientation: portrait) {
				width: 90%;
				@include cush(3px 5%, 5px);
				display: block; } } } }

.dropdown-toggle {
	//width: 200px
	text-align: center;
	font-size: 1.5em;
	border-radius: .5em;
	background-color: #121212; }

.dropdown-menu {
	box-shadow: 1px 1px #ffffff;
	border-radius: 10px; }
