//----- SECTION 3 ----->>>>>
#section3 {
  @include box-fluid;
  padding: 50px 20px;
  //min-height: 80vh
  color: #fff;
  background-color: #232323; }
.pages-menu {
  width: 100%;
  height: auto;
  padding-bottom: 50px;
  border-bottom: $border-color-green;
  ul {
    width: 100%;
    margin: 0;
    padding: 0;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    align-items: center;
    li {
      display: flex;
      flex: 1 1 30%;
      margin: 10px;
      padding: 5px 0;
      //color: #232323
      list-style: none;
      border: thin solid #262626;
      @media (max-width: 550px) {
        flex: 1 1 80%; }
      @media (min-width: 551px) and (max-width: 768px) {
        flex: 1 1 50%; }
      @media (min-width: 769px) {
        flex: 1 1 30%; }
      a {
        width: 100%;
        height: 100%;
        margin: 0;
        padding: 6px 0;
        text-align: center;
        letter-spacing: 1.25px;
        border: thin solid $clr-green;
        border-radius: 10px;
        @include font-link;
        color: #fff;
        text-decoration: none;
        background-color: $clr-green;
        &:hover {
          color: $clr-med-grey;
          text-decoration: none;
          background-color: $clr-dark-grey;
          border: thin solid $clr-green; } } } } }
.blackops2 {
  //@include full-box
  width: 80%;
  margin: auto;
  padding: 50px 0;
  a {
    //@include full-box
    width: 100%;
    img {
      width: 100%;
      height: auto;
      margin: auto;
      padding: 0;
      box-shadow: $box-shade-green;
      animation: myShot 5s ease-in-out 1s infinite; } }
  h2 {
    text-align: center;
    @include font-h2; } }
@keyframes myShot {
  0% {
    border-color: #fff; }
  50% {
    border-color: #333; }
  100% {
    border-color: #fff; } }
