iframe {
  width: 480px / 2;
  height: 270px / 2;
  margin: 20vh 10%;
  display: block;
  border-top: thin solid #fff;
  border-left: thin solid #fff;
  border-bottom: medium solid #333;
  border-right: medium solid #333;
  box-shadow: 3px 3px 6px $clr-green;

  @media (max-width: 550px) and (orientation: portrait) {
    width: calc( 480px / 1.75);
    height: calc( 270px / 1.75);
    margin: 5vh auto;
    display: block; }

  @media (min-width: 551px) and (max-width: 768px) and (orientation: portrait) {
    width: calc( 480px * 1);
    height: calc( 270px * 1);
    margin: 5vh auto;
    display: block; }

  @media (min-width: 769px) and (max-width: 992px) and (orientation: portrait) {
    width: calc( 480px * 1.5);
    height: calc( 270px * 1.5);
    margin: 5vh auto;
    display: block; }

  @media (min-width: 460px) and (max-width: 620px) and (orientation: landscape) {
    width: calc( 480px * .9);
    height: calc( 270px * .9);
    margin: 5vh auto;
    display: block; }

  @media (min-width: 621px) and (max-width: 800px) and (orientation: landscape) {
    width: calc( 480px * 1);
    height: calc( 270px * 1);
    margin: 5vh auto;
    display: block; }

  @media (min-width: 801px) and (max-width: 1080px) and (orientation: landscape) {
    width: calc( 480px * 1.5);
    height: calc( 270px * 1.5);
    margin: 5vh auto;
    display: block; }

  @media (min-width: 1081px) and (max-width: 1400px) and (orientation: landscape) {
    width: calc( 480px * 1.8);
    height: calc( 270px * 1.8);
    margin: 5vh auto;
    display: block; }

  @media (min-width: 1400px) and (orientation: landscape) {
    width: calc( 480px * 2.2);
    height: calc( 270px * 2.2);
    margin: 15vh auto;
    display: block; } }

.white-plush {
  background-image: url(../img/white-plush-1.jpg);
  @include bg-cover; }

.text-warning {
  width: 100%;
  height: auto;
  margin: 0;
  padding: 20px 10px 30px;
  text-align: center;
  color: #262626;
  border-top: $border-color-green;
  h2 {
    font-weight: 600;
    @include font-h2; }
  p {
    width: 90%;
    margin: auto 5%;
    padding-bottom: 10px;
    font-weight: 500;
    background: rgba(222, 222, 222, .7);
    border-radius: 5px;
    border: thin solid rgba(33, 33, 33, .3);
    //border-right: thin solid #333
    box-shadow: 4px 2px 1px $clr-green;
    @include font-p;
    //@media (max-width: 768px)
    //width: 90%
    //margin: auto 5%
    @media (min-width: 769px) {
      width: 80%;
      margin: 20px auto;
      padding: 20px; } } }
