//----- SECTION 2 ----->>>>>
#section2 {
  @include box-fluid;
  padding-top: 50px;
  padding-bottom: 50px;
  //min-height: 80vh
  color: #fff;
  background-color: $clr-green;
  font-size: 1.5em;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-items: flex-start;
  div {
    //width: 300px
    height: auto;
    padding: 10px;
    margin: 10px;
    background-color: $clr-dark-grey;
    text-align: center;
    border-radius: 15px; } }
img {
  width: 100%;
  height: auto;
  border-radius: 15px; }
.left-side {
  flex: 1 260px;
  height: auto;
  @include trans-1;
  border: thin solid $clr-med-grey;
  @media (min-width: 900px) {
    &:hover {
      border: thin solid #c9c9c9;
      z-index: 20;
      transform: scale(1.6) translateX(25%); } } }
.right-side {
  flex: 1 260px;
  height: auto;
  @include trans-1;
  border: thin solid $clr-med-grey;
  @media (min-width: 900px) {
    &:hover {
      border: thin solid #c9c9c9;
      z-index: 20;
      transform: scale(1.6) translateX(-25%); } }
  order: 3; }
.middle {
  flex: 1 260px;
  height: auto;
  @include trans-1;
  border: thin solid $clr-med-grey;
  @media (min-width: 900px) {
    &:hover {
      border: thin solid $clr-light-grey;
      z-index: 20;
      transform: scale(1.6); } } }

.text-graphic-1 {
  width: 100%;
  padding: 40px 0 45px;
  text-align: center;
  font-weight: bolder;
  color: #fff;
  text-shadow: $text-shade-green;
  background-color: #232323;
  border-bottom: $border-color-green;
  text-shadow: $text-shade-green;
  h1 {
    @include font-h1;
    font-style: italic; } }
